  .q-search{
    margin-top: 5vh;
    margin-left: 20vw;
  }
  .search{
    margin-top: 5vh;
    margin-left: 3vw;
  }
  .resault{
    margin-top: 5vh;
    margin-left: 2vw;
    margin-right: 2vw;
  }
